mic_settings.handle\n        ].meta;\n      }\n      return this.getFieldDefault().meta;\n    },\n  },\n  methods: {\n    getFieldDefault() {\n      return this.setDefaults[this.handle];\n    },\n  },\n  provide() {\n    return {\n      storeName: \"base\",\n    };\n  },\n};\n</script>\n\n<style scoped>\n.buildamic-field + .buildamic-field {\n  margin-top: 2rem;\n}\n</style>\n"],"sourceRoot":""}]);
